@import url('https://fonts.googleapis.com/css2?family=Roboto+Flex:wght@400;500;600;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Poppins:wght@400;500;700&display=swap');
@import url('https://fonts.googleapis.com/css2?family=Roboto+Slab:wght@300;400;600&display=swap');

//CSS Reset
*{
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

body{
    font-family: 'Roboto Flex', sans-serif;
    background-color: white;
    overflow-x: hidden;

    &::-webkit-scrollbar {
        width: .4rem;
      }
      
      &::-webkit-scrollbar-track {
         background-color: rgb(255, 255, 255);
      }
      
      &::-webkit-scrollbar-thumb {
       
        background: #dddbdb;
          border: 2px solid transparent;
          border-radius: 9px;
          background-clip: content-box;
      }

    a{
        text-decoration: none;
    }
    li{
        list-style: none;
    }
    button{
        border: none;
        outline: none;
    }
    .text-center{
        text-align: center;
    }
}

div .loading-gif{
    width: 100%;
    margin-top: -250px;
    z-index: 10;
    position: absolute;
}

//Header CSS


    .header{
        position: absolute;
        width: 100%;
        z-index: 10;
        height: 75px;

        .header-gradient{
            background: linear-gradient(0deg, rgba(0, 0, 0, 0) 0%, rgba(0,0,0,1) 100%);
            height: 14vh;
            width: 100%;
            position: absolute;
        }

        .logo{
            width: 9rem;
            height: 3.5rem;
            margin: 5px 0px 0px 12px;
            transition: all ease-in .5s;
            cursor: pointer;
            position: relative;
        }
        .menu-icon{
            width: 40px;
            height: 40px;
            margin: 16px 8px 0px 0px;
            cursor: pointer;
            position: absolute;
            right: 8px;
        }
        .login-btn{
            position: absolute;
            right: 75px;
            top: 18px;
            border-radius: 5px;
            font-size: 17px;
            color: white;
            font-weight: 600;
            padding: 7px 22px;
            opacity: 0.9;
            cursor: pointer;
            background: linear-gradient(45deg, #27d0f9, #3654ff);

            &:hover{
                background: linear-gradient(45deg, #1bbae2, #082ae9);
            }
        }
        }

        .no-icon{
            display: none;
        }
        .close-icon{
            width: 35px;
            height: 35px;
            margin: 16px 8px 0px 0px;
            cursor: pointer;
            position: absolute;
            right: 8px;
        }

    .show-items{
        height: 36.5vh;
        background-color: rgb(35, 37, 54);
        animation: slideIn 0.7s;
        margin: -65px 0 0 0;
        padding-top: 90px;

        @keyframes slideIn {
            0%{
                transform: translate(0px, -100%);
            }
            100%{
                transform: translate(0px, 0%);
            }
        }

        a{
            width: 105px;
            display: block;

          li{
                display: flex;
                padding: 8.5px 0px;
                margin: 0px 0px 0px 32px;

                h2{
                    color: white;
                    font-size: 22px;
                    font-weight: 600;
                    font-family: 'Lucida Sans', 'Lucida Sans Regular', 'Lucida Grande', 'Lucida Sans Unicode', Geneva, Verdana, sans-serif;
                    transition: all ease-in 0.3s;

                    &:hover{
                        color: rgb(131, 208, 231);
                    }
                }
                img{
                    margin-right: 8px;
                    width: 22px;
                    height: 22px;
                }
            }
        }
    }

    .hide-items{
        display: none;
    }

//************Hero & Card CSS*************\\

.hero{
    background-color: white;
    height: auto;

    .search-div{
        display: flex;
        height: 38px;
        width: 43%;
        position: absolute;
        left: 30%;
        top: 2.5%;
        z-index: 11;

        .search-bar{
            width: 88%;
            padding-left: 8px;
            font-size: 15px;
            border: none;
            outline: none;
            border-radius: 6px 0px 0px 6px;
        }

        .search-button{
            width: 55px;
            border-radius: 0px 6px 6px 0px;
            background-color: rgb(134, 134, 134);
            color: white;
            cursor: pointer;
            transition: all ease-in .3s;
            position: relative;

            img{
                position: absolute;
                top: 5px;
                left: 8px;
                width: 28px;
            }

            &:hover{
                background-color: rgba(66, 66, 66, 0.958);
            }
        }
    }
 
    .product{
        display: grid;
        grid-template-columns: repeat(auto-fill, minmax(260px, 1fr));
        grid-gap: 65px 35px;
        margin: 0 30px 0 30px;
        padding-bottom: 30px;
        min-height: 312px;
        padding-top: 60px;

        .no-product-info{
            position: absolute;   
            left: 35%;
            font-family: 'Poppins';
            
            h3{
                margin-left: 26.5%;
                font-size: 28px;
                font-weight: 400;
                width: 400px;       
            }
            h5{
                font-size: 14px;
                font-weight: 400;
                color: rgb(85, 84, 84);
                margin-left: 40px;
            }
        }

        .product-wall-parent{
            position: absolute;
            width: 95.5%;
            height: 5vh;
            top: 53.2%;
            text-align: center;
            display: flex;
            justify-content: center;

            .total-items{
                position: absolute;
                left: 20.9%;
                top: -41%;
                font-size: 17px;
                color: #324949;
                font-family: "Poppins";
                font-weight: 500;

                span{                   
                    color: #2aade2;
                    font-size: 28px;
                }
            }

            .drop-icon{
                position: absolute;
                top: -47%;
                left: 0%;
                width: 47px;
                border-radius: 30px;
                cursor: pointer;
                transition: all ease 0.3s;
                z-index: 10;

                &:hover{
                    transform: scale(1.1);
                }
            }

            h2{
                font-size: 17px;
                font-weight: 800;
                text-align: center;
                padding: 4px;
                width: 108px;
                position: absolute;
                top: -22%;
                left: 48px;
                border-radius: 4px;
                border: 1.5px solid #413f3f;
            }

            .product-wall{
                position: absolute;
                top: -33.5%;
                width: 300px;
                height: 85px;
            }
            .cart-btn{
                display: flex;
                position: absolute;
                right: -3%;
                top: -150%;
                margin-right: 35px;            
                padding: 11px 26px;
                background-color: rgb(40, 40, 62);
                border-radius: 7px;
                cursor: pointer;
                margin-top: 38px;

                &:hover{
                    background-color: rgb(31, 31, 108);
                }

                h3{
                    font-size: 20px;
                    color: white;
                }
                img{
                    width: 25px;
                }
                .cart-count{
                    position: absolute;
                    color: rgb(21, 0, 255);
                    left: 70%;
                    top: 22%;
                    padding: 0 3px;
                    font-size: 14px;
                    background-color: aqua;
                    border-radius: 10px;
                }
            }
        }

        li{
            height: 330px;
            width: 260px;
            border-radius: 7px;
            position: relative;
            transition: .5s;
            margin: 0 auto;
            &:hover{
                transform: scale(1.04);
            }

            .thumbnail{
                min-height: 240px;
                height: 240px;
                min-width: 260px;
                width: 260px;
                object-fit: cover;
                border-radius: 7px;
                position: relative;
                cursor: pointer;
                box-shadow: 1px -1px 5px #646464;
            }
            h5{
                position: absolute;
                top: 65%;
                right: 1%;
                color: rgb(255, 255, 255);
                background-color: rgb(46, 46, 46);
                padding: 3px 6px;
                border-radius: 7px;
            }
            .title{
                color: rgb(32 30 63);
                font-size: 22px;
                margin-top: 3px;
                margin-left: 10px;
                font-family: Arial, Helvetica, sans-serif;
                max-height: 28px;
                overflow: hidden;
            }

            .card-items{
                font-family: 'Roboto Slab', serif;
                margin-top: -2px;

                .price-offer{
                    font-size: 22px;
                    font-weight: 600;
                    color: rgb(56 47 106);
                    margin: 2px 0px 8px 6px;

                    span{
                        font-size: 16px;
                        margin-right: 2px;
                    }
                }
                .price{
                    font-size: 12px;
                    color: rgb(55, 51, 51);
                    margin-top: -8px;
                    text-decoration: line-through black;
                    font-weight: 500;
                    margin-left: 6px;
                    
                    span{
                        color: rgb(55, 51, 51);
                        font-size: 10px;
                    }
                }
                .price-icon{
                    height: 23px;
                    width: 23px;
                    position: absolute;
                    left: -4.5%;
                    margin-top: -29px;
                }
            }
            .rating{
                display: flex;
                position: absolute;
                left: 56%;
                top: 83.8%;

                img{
                    width: 65px;
                    height: 13px;
                }
                h6{
                    font-size: 13px;
                    font-weight: 500;
                    color: #ff7a00;
                    margin-left: 5px;
                }
            }

            button{
                padding: 8px 16px;
                border-radius: 20px;
                outline: none;
                cursor: pointer;
                position: absolute;
                left: 55%;
                top: 89.3%;
                color: white;
                font-weight: 700;
                background-color: #3387fb;
                z-index: 9;
                transition: all ease-in-out 0.3s;

               &:hover{
                 background-color: #2b11d3;
               }
           }
        }}}

.popup{
    position: absolute;
    top: 0;
    bottom: 0;
    left: 0;
    right: 0;
    background-color: #0c0c0cac;
    z-index: 10;
    height: 10426px;

    .popup-content{
        position: fixed;
        height: 500px;
        width: 450px;
        background-color: #b4f7fd;
        transform: translate(-50%, 25%);
        left: 50%;
        border-radius: 10px;
        box-shadow: 5px 5px 20px black;

        .close-icon{
            width: 35px;
            margin-right: 8px;
            cursor: pointer;
            position: absolute;
            left: 100%;
            top: -3%;
        }

        .images-list{
            position: absolute;
            width: 75%;
            height: 75%;
            top: 12%;
            left: -36%;
            border-radius: 10px;
            box-shadow: 0.5px 0.5px 5px rgb(147, 145, 145);
            background-color: white;

            .product-thumbnail{
                border-radius: 10px 10px 0 0;
                width: 100%;
                height: 80%;
            }

            .sub-images-list{
                display: flex;
                margin-top: 5px;
                margin-left: 22px;

                .product-images{
                    width: 54px;
                    height: 54px;
                    border-radius: 5px;
                    margin-right: 20px;
                    border: 1px solid rgb(211, 211, 211);
                    box-shadow: .5px .5px 1px rgb(124, 124, 124);
                }
            }
          }
        .text-list{
            margin-left: 182px;
            margin-top: 30px;
            width: 260px;

            .product-title{
                font-size: 26px;
                min-height: 40px;
                text-align: center;
                color: rgb(42, 40, 76);
                font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
            }
            .title-line{
                padding: .1px 1px;
                margin: 12px 10px;
                background-color: black;
            }
            .product-category{
                position: absolute;
                top: 67.5%;
                left: 17%;
                font-size: 13px;
                background-color: rgba(50, 57, 114, 0.853);
                padding: 1px 8px;
                border-radius: 8px;
                color: white;
            }
            .product-brand{
                font-size: 14.5px;
                text-align: center;
                margin: 5px;
                color: rgb(41, 46, 82);
                font-family: Cambria, Cochin, Georgia, Times, 'Times New Roman', serif;
            }
            .price-quantity-div{
                display: flex;
                margin: 25px 15px 15px 15px;
                text-align: center;
                width: 185%;
                font-family: 'Roboto Slab', serif;;

                h5{
                    font-size: 13px;
                    color: rgb(255, 255, 255);
                    background-color: rgb(228, 25, 59);
                    margin: 0px 62px;
                    padding: 3.5px 0;
                    border-radius: 3px;
                }
                .price-div{
                    width: 50%;

                    .product-price-offer{
                        font-size: 30px;
                        font-weight: 600;
                        color: rgb(47 41 82);
                        margin: 5px 0 8px -10px;

                        span{
                            font-size: 18px;
                            margin-right: 2px;
                        }
                    }

                    .product-price{
                        font-size: 14px;
                        color: rgb(55, 51, 51);
                        margin-right: 8px;
                        margin-top: -4px;
                        text-decoration: line-through black;
                        font-weight: 500;
                        
                        span{
                            color: rgb(55, 51, 51);
                            font-size: 12px;
                        }
                    }
                }
            }

            .product-rating{
                display: flex;
                flex-direction: column;
                margin: 15px;
                text-align: center;
                font-family: 'Roboto Slab', serif;

                .rating-top{
                    display: flex;
                    padding: 0 0 5px 67px;

                    img{
                        width: 70px;
                        height: 16px;
                    }
                    h6{
                        font-size: 13px;
                        font-weight: 500;
                        color: #ff7a00;
                        margin-left: 5px;
                    }}
                    h4{
                        font-size: 13px;
                        color: #ff5e00;
                    }
                }

            .product-stock{
                color: green;
                text-align: center;
                font-size: 16px;
            }

            .product-description{
                font-size: 15px;
                font-weight: 500;
                margin: 10px 15px 15px 15px;
                min-height: 70px;
                text-align: center;
                color: rgb(54, 53, 53);
            }
        }
        .btn-parent{
            text-align: center;

        .add-btn{
            padding: 15px 24px;
            font-size: 17px;
            font-weight: 600;
            color: white;
            border-radius: 7px;
            cursor: pointer;
            margin-top: 20px;
            background-color: rgb(244, 64, 196);
            transition: all ease 0.3s;

            &:hover{
                background-color: rgb(255, 0, 153);
            }
        }}
    }
}

//AddToCart CSS

.add-to-cart-wrapper{
    font-family: 'Poppins', sans-serif;
    overflow: hidden;
    height: 100vh;

    .background-color{
        background-color: rgb(35, 37, 54);
        height: 80px;
    }

    header {
        width: 200%;
        margin-bottom: 2rem;

        .header-bar{
            display: flex;
            align-items: center;
            margin: 20px 120px;
            width: 41%;
            padding-bottom: 15px;
            border-bottom: 1px solid #818181;;

            .arrow-icon{
                width: 16px;
                height: 14px;
                cursor: pointer;
            }
            h3{
                font-size: 16px;
                text-transform: capitalize;
                color: #807e7e;
                margin-left: 10px;
                font-weight: 400;
            }
            .cart-icon{
                width: 40px;
                position: absolute;
                right: 10.5%;
            }
            .count{
                position: absolute;
                right: 10.1%;
                padding: 2px 6px;
                color: white;
                font-size: 12px;
                border-radius: 15px;
                margin-bottom: 24px;
                background-color: rgb(29, 120, 189);
            }
        }
    }

    .add-to-cart-items{
        margin: 20px 120px;
        height: 77vh;
        
        h2{
            font-size: 22px;
            font-weight: 500;
        }
        p{
            font-size: 13px;
            margin: 3px 0;
            color: #545454;
        }

        .cart-items{
            background-color: #f1f1f174;
            height: 57vh;
            width: 97.5%;
            margin: 20px 0;
            box-shadow: 1px 1px 5px #bdbcbc;
            padding: 20px 40px;
            overflow-y: scroll;

            .cart-items-list{
                display: flex;
                align-items: center;
                height: 11vh;
                padding: 20px 0;
                border-bottom: 1px solid #949494;

                .cart-image{
                    width: 120px;
                    height: 65px;
                    margin: -12px 60px 0 50px;
                    margin-top: 0;
                }

                .product-title{
                    text-align: center;
                    width: 300px;
                    overflow: hidden;

                    .cart-title{
                        font-size: 18px;
                    }
                    .cart-category{
                        font-size: 13px;
                        font-weight: 500;
                }
            }

            .cart-quantity{
                display: flex;
                justify-content: center;
                width: 300px;

                .count{
                    margin: 0 10px;
                }
                img{
                    cursor: pointer;
                }
            }

            .cart-price{
                font-size: 19px;
                width: 200px;
                text-align: center;
                font-family: 'Times New Roman', Times, serif;
                font-weight: 600;
            }
            .delete-icon{
                margin-left: 70px;
                cursor: pointer;
            }
        }
        .empty-cart{
            width: 100%;
            height: 100px;
            text-align: center;
            margin-top: 9.5rem;

            h4{
                font-weight: 400;
                font-size: 12px;
            }
        }
    }
    .cart-total{
    
    h2{
        font-size: 19px;
        margin-left: 83%;
        margin-top: -10px;
        font-family: 'Times New Roman', Times, serif;
        font-weight: 600;
      }

    .checkout{
        margin-left: 90%;
        margin-top: 3px;
        background-color: #3387fb;
        padding: 6px 16px;
        border-radius: 5px;
        color: white;
        font-weight: 600;
        cursor: pointer;

        &:hover{
            background-color: #006aff;
        }
    }
   }
}
}

//Contact CSS
.container{
    width: 100%;

    .background-color{
        background-color: rgb(35, 37, 54);
        height: 80px;
    }


.contact{
    height: auto;
    width: 1050px;
    display: flex;
    margin: 0 auto;

    &-form{
        background-color: #112f49;
        height: 100vh;
        width: 570px;
        border-radius: 15px;
        margin: 40px 20px;
        color: white;

        h1{
            font-size: 25px;
            font-weight: 500;
            margin: 40px 0 0 30px; 
        }

        .send-btn{
            padding: 12px 20px;
            border-radius: 8px;
            font-size: 17px;
            font-weight: 200;
            background-color: #6969f6;
            border: none;
            outline: none;
            color: white;
            cursor: pointer;
            transition: all ease-in .3s;
            margin: -40px 0 30px 0;
    
            &:hover{
                background-color: #2626ec;
            }
        }

        .forms{
            margin: 35px;

            label h3{
                font-size: 18px;
                font-weight: 200;
                margin-bottom: 5px;
                color: white;
            }

            input{
                background-color: rgb(30 56 81);
                border: none;
                width: 100%;
                height: 45px;
                margin: 0 2px 20px 2px;
                border-radius: 8px;
                padding-left: 13px;
                font-size: 17px;
                color: white;
                border: 1px solid rgba(50, 74, 74, 0.689);
            }
            #message-input{
                height: 220px;
                
            }
        }
    }

    &-details{
        margin-left: 50px;
        margin-top: 60px;
        width: 400px;

        h1{
            font-size: 25px;
            font-weight: 500;
            margin-bottom: 40px;
            color: #112f49;
        }

        .details{
            display: flex;
            position: relative;
            margin-bottom: 20px;

            svg{
                color: #112f49;
                display: inline-block;
                position: absolute;
                height: 24px;
                width: 24px;
            }

            h3{
                margin-left: 40px;
                font-size: 18px;
                font-weight: 200;
                color: #112f49;
            }
        }

        .google-map{
            iframe{
                width: 300px;
                height: 400px;
                margin: 30px 0;
                border-radius: 10px;
            }
        }
    }
}
}

//About CSS

.about{
    width: 100%;
    border: 50px solid #232536;
    border-top: 0;

    .background-color{
        background-color: #232536;
        height: 80px;
    }

    .about-disclaimer{
        height: 83vh;
        width: 100%;
        display: flex;
        align-items: center;
        justify-content: center;

        img{
            width: 300px;
        }
    }
}

.categories-hide{
    display: none;
}

.categories{ 
    font-family: 'Poppins', sans-serif;
    animation: slideOut .3s;

    @keyframes slideOut {
        0%{
            transform: translate(-50%, -66%);
        }
        100%{
            transform: translate(-8%, -66%);
        }
    }

    .side-bar{
        width: 295px;
        height: 53vh;
        position: absolute;
        left: 0px;
        top: 47%;
        padding: 9px 16px 16px 1%;

        .side-content{
            padding: 70px 0 0 70px;

            h4{
                font-size: 15.5px;
                font-weight: 500;
                padding: 4.3px 0;
                color: #000000c7;
                cursor: pointer;
                transition: all ease-in-out 0.3s;

                &:hover{
                    color: #1c8b95ee;
                    transform: scale(1.05);
                }
            }
        }
}
}


//************Login & Register CSS*************\\
.login-page{
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: center;
    margin-top: 30px;

  #logo-hm{
    width: 130px;
    height: 60px;
    cursor: pointer;
  }

   form{
    width: 350px;
    height: auto;
    border: .1px solid lightgrey;
    border-radius: 8px;
    padding: 20px;
    margin-top: 10px;
      
      h2{
        font-size: 28px;
        font-weight: 400;
      }

      .form-content{
        display: flex;
        flex-direction: column;
        margin-top: 30px;

        label{
            font-size: 14px;
            font-family: Arial, Helvetica, sans-serif;
            font-weight: 600;
          }

        input{
            padding: 8px;
            border-radius: 3px;
            border: 1px solid lightgrey;
            outline: none;
            margin-top: 3px;
            margin-bottom: 12px;
            font-size: 15px;
        }
      }

      #login-btn{
        color: white;
        font-size: 16px;
        font-weight: 600;
        border: none;
        outline: none;
        border-radius: 5px;
        background-color: rgba(1, 176, 219, 0.971);
        padding: 9px 132px;
        margin: 10px 0;
        text-shadow: .5px .5px 1px black;
        transition: all ease 0.3s;

        &:hover{
            background-color: rgba(1, 100, 206, 0.971);
            color: rgba(255, 255, 255, 0.926);
        }
      }

      #register-btn{
        color: white;
        font-size: 16px;
        font-weight: 600;
        border: none;
        outline: none;
